var select = document.querySelector('.selector');
var row = document.querySelector('.row');
var areaTitle = document.querySelector('.area-title');
var hotList = document.querySelector('.hotlist-group');
var pageBar = document.querySelector('.pagebar');
var pageGroup = document.querySelector('.pagebar-group');

function cardProcess(item, count) {
    var areaInfo = {
        Image: item.Picture1,
        Time: item.Opentime,
        Address: item.Add,
        Tel: item.Tel,
        Ticket: item.Ticketinfo
    }
    var cardUrl = 'background-image: url(' + areaInfo.Image + ');'
    var cardImage = '<div class="infocard-img" style="' + cardUrl + '"></div>';
    var cardTime = '<li><img src="images/icons_clock.png">' + areaInfo.Time + '</li>';
    var cardAddress = '<li><img src="images/icons_pin.png">' + areaInfo.Address + '</li>';
    var cardTel = '<li class="infocard-phone"><img src="images/icons_phone.png">' + areaInfo.Tel + '</li>';
    var cardTag = '<div class="infocard-tag"><img src="images/icons_tag.png" alt="">' + areaInfo.Ticket + '</div>';
    var cardContent = '<div class="infocard-content"><ul>' + cardTime + cardAddress + cardTel + '</ul></div>';
    var cardItem = '<div class="infocard-item">' + cardContent + cardTag + '</div>';
    var card = '<div class="infocard">' + cardImage + cardItem + '</div>';
    
    var colHalf = document.createElement('div');
    colHalf.setAttribute('class', 'col-50');
    colHalf.setAttribute('data-num', count);
    colHalf.innerHTML = card;
    row.appendChild(colHalf);
}

function pageBarCreate(start, count) {
    pageGroup.innerHTML = '';
    if(start + 4 <= count)
        for(var x = start; x <= start + 4; x++) {
            var strPage = '<li class="pagebar-item"><a href="#" class="font-gray">' + x + '</a></li>';
            pageGroup.innerHTML = pageGroup.innerHTML + strPage;
        }
    else
        for(var x = start; x <= count; x++) {
            var strPage = '<li class="pagebar-item"><a href="#" class="font-gray">' + x + '</a></li>';
            pageGroup.innerHTML = pageGroup.innerHTML + strPage;
        }
}

function calPage(count) {
    if(count / 8 > parseInt(count / 8) || count / 8 == 0){
        return parseInt(count / 8) + 1;
    } else {
        return parseInt(count / 8);
    }
}

function variableInit() {
    row.innerHTML = '';
    itemCount = 0;
    pageFlag = 1;
}

function init() {
    row.innerHTML = '';

    dataList.forEach(function(item){
        itemCount = itemCount + 1;
        if(itemCount >= pageFlag && itemCount < pageFlag + 8)
            cardProcess(item, itemCount);
        if(areaName.indexOf(item.Zone) != -1) {return}
        areaName.push(item.Zone);
        select.innerHTML = select.innerHTML + '<option value="' + item.Zone + '">' + item.Zone + '</option>';
    });
    
    countAllItem = itemCount;
    pageCount = calPage(countAllItem);
    pageBarCreate(1, pageCount);
}

function printData(value) {
    if(value === '') {
        dataList.forEach(function(item){
            itemCount = itemCount + 1;
            if(itemCount >= pageFlag && itemCount < pageFlag + 8)
                cardProcess(item, itemCount);
        });
    } else {
        dataList.forEach(function(item){
            if(item.Zone === areaTitle.textContent) {
                itemCount = itemCount + 1;
                if(itemCount >= pageFlag && itemCount < pageFlag + 8)
                    cardProcess(item, itemCount);
            }   
        });
    }
}

var areaName = [];

var itemCount = 0;
var countAllItem = 0;
    
var pageCount = 0;
var pageFlag = 1;
var pageGroupCount = 0;
    

init();

select.addEventListener('change', function(event){
    variableInit();
    if(event.target.value === 'none'){
        areaTitle.textContent = '';
        printData(areaTitle.textContent)
        pageCount = calPage(countAllItem);
        pageBarCreate(1, pageCount);
    } else {
        areaTitle.textContent = event.target.value;
        printData(areaTitle.textContent);
        pageCount = calPage(itemCount);
        pageBarCreate(1, pageCount);
    }
});

hotList.addEventListener('click', function(event){
    event.preventDefault();
    variableInit();
    areaTitle.textContent = event.target.textContent;
    printData(areaTitle.textContent);
    pageCount = calPage(itemCount);
    pageBarCreate(1, pageCount);
    if(areaName.indexOf(areaTitle.textContent) > -1)
        select[areaName.indexOf(areaTitle.textContent) + 1].selected = true;
});

pageBar.addEventListener('click', function(event){
    event.preventDefault();
    if(event.target.nodeName === 'A' && event.target.textContent != '< prev' && event.target.textContent != 'next >') {
        pageFlag = 1 + (parseInt(event.target.textContent) - 1) * 8;
        row.innerHTML = '';
        itemCount = 0;
        printData(areaTitle.textContent);
    } else if(event.target.textContent === '< prev') {
        if(pageGroupCount <= 0) {return}
        pageGroupCount = pageGroupCount - 1;
        pageFlag = 1 + pageGroupCount * 5 * 8;
        row.innerHTML = '';
        itemCount = 0;
        printData(areaTitle.textContent);
        pageBarCreate(1 + pageGroupCount * 5, pageCount);
    } else if(event.target.textContent === 'next >') {
        if(pageGroupCount >= parseInt(pageCount / 5)) {return}
        pageGroupCount = pageGroupCount + 1;
        pageFlag = 1 + pageGroupCount * 5 * 8;
        row.innerHTML = '';
        itemCount = 0;
        printData(areaTitle.textContent);
        pageBarCreate(1 + pageGroupCount * 5, pageCount);
    }
});